import React, { Component } from 'react';
// import styled, { css, keyframes, ThemeProvider } from 'styled-components';
// import { A } from 'theme/types';
// import media from 'theme/media';
// import { connect } from 'react-redux';
// import { push } from 'react-router-redux';
// import { open as openMenu, close as closeMenu } from 'redux/modules/menu';
// import { Flex, Div, Relative } from 'theme/grid';
// import Network from 'components/Network/Network';
// import { easeInOutQuart, easeOutQuart } from 'theme/easeFunctions';
import { ProjectContainer, Content, CloseButtonContainer, Image, ButtonFlex, ButtonDiv, ProjectContainerR } from './Project.style'
import FontAwButton from "../FontAwButton/FontAwButton";


class Project extends Component {
  _renderCloseButton() {
    return (
      <ButtonFlex
        row
        justify={'center'}
        alignItems={'center'}
      >
        <ButtonDiv>
          <i class='fa fa-times' aria-hidden='true'></i>
        </ButtonDiv>
      </ButtonFlex>
    )
  }

  render() {
    return (
      <ProjectContainer>
        <Content>
        {/*<Image src={require('../../assets/close.svg')} />*/}
          {/*{this._renderCloseButton()}*/}
        <CloseButtonContainer onClick={() => this.props.closeProject(false)}>
          <FontAwButton
                        class={'fa fa-times'}/>
        </CloseButtonContainer>

        <FontAwButton class={'fa fa-angle-double-up'}/>
        </Content>
      </ProjectContainer>
    );
  }
}

export default Project;



