import React, { Component } from 'react';
import { ProjectContainer, ProjectItem, ProjectWrapper } from './ProjectGrid.style';
import Project from '../Project/Project';

class ProjectGrid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showProject: false
    }
  }

  toggleProject(status) {
    console.log('closeProject: ')
    this.setState({
      showProject: status
    })
  }

  render() {
    return (
      <ProjectWrapper>
        <h1>Projects</h1>
        <hr/>
        <ProjectContainer
          row
          justify={'center'}
          align={'center'}
        >
          {this.state.showProject &&
            <Project
              closeProject={this.toggleProject.bind(this)}
            />
          }

          <ProjectItem
            onClick={() => this.toggleProject.call(this, true)}
          >123</ProjectItem>
          <ProjectItem>456</ProjectItem>
          <ProjectItem>abc</ProjectItem>
          <ProjectItem>def</ProjectItem>
          <ProjectItem>ghi</ProjectItem>
        </ProjectContainer>
      </ProjectWrapper>
    );
  }
}

export default ProjectGrid;


